.dashboard {
	background-color: darken($medium-grey,70%);
	flex-direction: column;

	&-tabs {
		flex: 1;
		position: relative;
	}
	&-tab {
		height: 100%;
		left: 0;
		position: absolute;
		top: 0;
		width: 100%;
	}
}



